.Hero{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 152.5px;
}
.Herofin{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 100px;
}
.type-effect{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.hero-text{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#cursor-line{
    display: inline-block;
    height: 80px;
    width: 3px;
    background-color: black;
    margin-left: 20px;
    margin-bottom: 0px;
    animation: blink .6s linear infinite alternate;
}

#introduction{
    color: black;
    font-size: 70pt;
    margin-bottom: 0px;
    margin-top: 10px;

   
}

@keyframes blink{
    0% {opacity: 1;}
    40% {opacity: 1;}
    60% {opacity: 0;}
    100% {opacity: 0;}
}

.about-text{
    font-size: 30pt;
}


.fadein{
    animation: fadeIn 2s;
}

@keyframes fadeIn{
    0% {opacity: 0};
    100% {opacity: 1};
}

.hero-image{
    border-radius: 50%;    
    height: 500px;
    width: 500px;
}

