.Navbar{
    display: flex;
    justify-content: center;
    padding: 20px 50px;
    border-bottom: black solid 1px;
}

.NavLink{
    padding: 0px 30px;
    color: black;
    font-size: 14pt;
    text-decoration: none;
}


.NavLink:hover{
    cursor: pointer;
    text-decoration: underline;
}


span{
    animation: blinker 1s linear infinite
}

@keyframes blinker{
    0%{
        opacity: 1;
    }
    40%{
        opacity: 1;
    }
    80%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }
}
